.header {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
    position: relative;
    top: 0;
    margin: 0;
    height: 5vh;
    min-height: 5vh;
    width: 100%;
    overflow: hidden;
}

.user_group {
    display: -ms-flexbox;
    display: flex;
}

.header h1  {
    font-family: "proxima-nova", sans-serif;
    font-weight: 100;
    font-weight: 700;
    letter-spacing: -2px;
    float: left;
    line-height: 2px;
    padding-left: 25px;
    color: #f67600;
}
.link {
    border:none;
    outline:none;
    text-decoration:none;
    color:inherit;
    -webkit-tap-highlight-color: white;
}




