
.container {
    height: 4vh;
    background-color: #bcbcbc;
    width: 100%;
}
.tab_container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    
    width: 25%;
    height: 100%;
}

.tab {
    -ms-flex-preferred-size: 0;
        flex-basis: 0;
    -ms-flex-positive: 1;
        flex-grow: 1;
    font-family: "proxima-nova", sans-serif;
    font-weight: 100;
    text-transform: uppercase;
    margin-top: auto;
    text-align: center;

    cursor: pointer;

    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;

}


