.group1 .button {
    background-color: #646464;
    max-width: 15px;
    max-height: 15px;
}

.group1 .button:hover {
    color: white;
}

.group1 .button:active {
    background-color: #747474;
}

.group1 {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    height: 15px;
    width: 100%;
}

.group1 .horizontalTrack {
    background-color: gray;
    height: 15px;
    width: 100%;
}

.group1 .horizontalTrack .horizontalThumb {
    -webkit-box-sizing: border-box;
            box-sizing: border-box; 
    width: 50px;
    height: 15px;
    background-color: rgb(77, 77, 77);
    border: solid;
    border-radius: 0.1rem;
    border-color: #5c5c5c;
    border-width: 2px;
}

.group1 .horizontalTrack .horizontalThumb:hover {
    background-color: rgb(81, 81, 81);
}



